export var request = function (options) {
    var type = options.type;
    var path = options.url;
    var data = options.data;
    var headers = options.headers || {};
    var isFormData = function (data) {
        return data instanceof FormData;
    };
    return new Promise(function (reslove, reject) {
        if (['GET', 'POST'].indexOf(type) < 0) {
            reject(new Error('只持支 GET POST'));
        }
        var xhr = new XMLHttpRequest();
        xhr.timeout = 150000;
        xhr.onload = function () {
            var status = xhr.status;
            var response;
            try {
                response = JSON.parse(xhr.response);
            }
            catch (e) {
                response = xhr.response;
            }
            if (status >= 200 && status <= 300) {
                if (response.code === 0) {
                    reslove(response.data);
                }
                else {
                    console.error("" + response.data.message);
                    reslove(response.data);
                }
            }
            else {
                var responseText = xhr.statusText;
                console.error(responseText + "(" + status + ")");
                reslove(responseText);
            }
        };
        var body = null;
        body = isFormData(data) ? data : JSON.stringify(data);
        xhr.open(type, path, true);
        console.log(type, path);
        Object.keys(headers || {}).forEach(function (val) {
            xhr.setRequestHeader(val, (headers || {})[val]);
        });
        if (!isFormData(data)) {
            xhr.setRequestHeader('Content-Type', 'application/json;charset=utf-8');
        }
        xhr.send(body);
    });
};
